
.cardContainer{
    margin: 3rem 5rem;
    height: auto;
    padding-bottom: 5rem;
    text-align: center;
    background-size: contain;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    overflow: hidden;
}

