.serviceImg{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
}

.serviceTab{
    width: 10rem;
    height: 20rem;
    transition: 0.2s all linear;
    cursor: pointer;
    padding: 2rem 5px;
}

.serviceTab:hover{
    width: 15rem;
}

.servicesContainer{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    font-size: 0.8rem;
    width: 85%;
    margin: 0 auto;
}
