.mainContainer{
    background-color: #ccc;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    background-position: center;
    background-size: cover;
    justify-content: space-between;
}

.mainText{
    width: 36%;
    height: 60%;
    box-sizing: border-box;
    color: white;
    letter-spacing: 2px;
}

.mainTextRight{
    display: flex;
    justify-content: center;
    align-items: center;
}

.mainImage{
    width: 20%;
    height: 80%;
    background: #ffffffad;
    display: flex;
    align-items: center;
    justify-content: center;
}

.mainImage>img{
    width: 65%;
}