@font-face {
  font-family: 'Nobel';
  font-style: normal;
  font-weight: normal;
  src: local('Nobel Book'), url('./fonts/nobel/Nobel\ Book.woff') format('woff');
}

@font-face {
  font-family: 'Ruda';
  font-style: normal;
  font-weight: normal;
  src: local('Ruda'), url('./fonts/ruda/Ruda-Regular.woff') format('woff');
}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  box-sizing: border-box;
  font-family: 'Ruda';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* width */
::-webkit-scrollbar {
  width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
  background: white; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #333;
  border-radius: 5%;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.banner{
  width: 50rem;
  background: #e2e2e2;
  margin: 0 auto;
  text-align: center;
  padding: 1rem;
  box-shadow: 0px 10px 20px -10px #6a6a6a;
}

.banner>p{
  font-size: 2rem;
  letter-spacing: 0.3rem;
  text-transform: uppercase;
}

.marTop{
  margin-top: 5rem;
}

.mainMarTop{
  margin-top: 60px;
}

.marTopXs{
  margin-top: 20px;
}

.hTextSmall{
  text-transform: uppercase;
  font-size: 22px;
  margin-bottom: 12px;
  letter-spacing: 2px;
}

.sideImg{
  width: 25%;
  background-position: center;
  background-size: cover;
}

.imgMain{
  background-image: url("./imgs/main.jpg");
}

.imgServices{
  background-image: url("./imgs/services.jpg");
}

.imgContact{
  background-image: url("./imgs/contact.jpg");
}

.imgAbout{
  background-image: url("./imgs/about.jpg");
}
