.paddingBox{
    padding: 3rem 5rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.serviceMenuList{
    list-style-type: none;
    width: 20%;
    height: 100%;
    background: #e2e2e2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 1%;
    box-sizing: border-box;
}

.serviceMenuList>li {
    padding: 5px;
}

